/**
 * Layout: Password
 * -----------------------------------------------------------------------------
 * Entry file for all global password scripts.
 *
 */

import '@/layout/password.scss'

/**
 * Core scripts.
 */
import '~/core/lazysizes'
import Bugsnag from '~/core/bugsnag'
import Canvas from '~/core/canvas'
import Commands from '~/core/commands'

/**
 * Global Canvas functions.
 */
window.cnvs = window.cnvs || {}

const selectors = {
  loginFormToggle: '[js-toggle="loginFormToggle"]',
  loginForm: 'loginForm',
}



document.addEventListener('DOMContentLoaded', () => {
  const nodeSelectors = {
    loginFormToggle: document.querySelector(selectors.loginFormToggle),
    loginForm: document.getElementById(selectors.loginForm),
  }

  passwordFormToggle(nodeSelectors)
  Bugsnag().init()
  Commands().init()
  Canvas().init()
})

function passwordFormToggle(nodeSelectors) {
  nodeSelectors.loginFormToggle.addEventListener('click', () => {
    if (nodeSelectors.loginForm.classList.contains('is-active')) {
      nodeSelectors.loginForm.classList.remove('is-active')

      return
    }

    nodeSelectors.loginForm.classList.add('is-active')
  })
}